import { Component } from '@angular/core';

@Component({
	selector: 'page-how-made',
	templateUrl: 'how-made.component.html',
	styleUrls: ['how-made.component.scss'],
})
export class HowMadeComponent {
	public type: string = 'natura';

	public scrollTo(id: string, headerOffset: number = 0) {
		setTimeout(() => {
			const el = document.getElementById(id);
			if (el) {
				const elementPosition = el.offsetTop;
				const offsetPosition = elementPosition - headerOffset;
				window.scrollTo(0, offsetPosition);
			}
		}, 100);
	}

}
