import { Component, OnInit, ChangeDetectionStrategy } from '@angular/core';
import { SwiperConfigInterface, SwiperNavigationInterface, SwiperPaginationInterface } from 'ngx-swiper-wrapper';
import { CMSService, Categories } from '@app/cms';
import { combineLatest, Observable } from 'rxjs';
import { tap, map, withLatestFrom, filter } from 'rxjs/operators';

@Component({
	selector: 'section[products]',
	templateUrl: 'products.component.html',
	styleUrls: ['products.component.scss'],
	changeDetection: ChangeDetectionStrategy.OnPush
})
export class ProductsComponent implements OnInit {

	public readonly threeColSliderConfig: SwiperConfigInterface = {
		observer: true,
		slidesPerView: 3,
		navigation: {
			nextEl: '.slider__products .swiper-button-next',
			prevEl: '.slider__products .swiper-button-prev',
		},
		pagination: {
			el: '.slider__products .swiper-pagination',
			type: 'bullets',
		},
		breakpoints: {
			576: {
				slidesPerView: 1
			},
			768: {
				slidesPerView: 2
			}
		}
	};

	public categories$: Observable<Categories>;

	constructor(
		private _cms: CMSService,
	) {}

	ngOnInit(): void {
		const categories$ = this._cms.getCategories();

		this.categories$ = combineLatest(
			categories$,
		).pipe(
			// wait until everyting is loaded
			filter(([categories]) => categories.length > 0),
			// add packshot data to category
			map(([categories]) => categories
				.map(category => {

					let slug = category.slug;
					let mainRoute = '/termekek';
					if (category.slug == 'juice') {
						mainRoute = '/juice';
						slug = '';
					}

					return {
						...category,
						mainRoute,
						slug,
					};
				})
			),
			tap(x => console.log('categories with home page', x)),
		);

		/*let cats = this._cms.getCategories();
		const categs = [];
		cats.forEach((cat) => {
			let slug = cat.slug;
			let mainRoute = '/termekek';
			if (cat.slug == 'juice') {
				mainRoute = '/juice';
				slug = '';
			}

			categs.push([
				...cat,
				slug,
				mainRoute
			]);
		});
		this.categories$ = categs;*/
	}
}
