import { Component, ChangeDetectionStrategy, OnInit } from '@angular/core';
import { SwiperConfigInterface } from 'ngx-swiper-wrapper';
import { CMSService, Entity } from '@app/cms';
import { Observable } from 'rxjs';

@Component({
	selector: 'section[hero]',
	templateUrl: 'hero.component.html',
	styleUrls: ['hero.component.scss'],
	changeDetection: ChangeDetectionStrategy.OnPush
})
export class HeroComponent implements OnInit {

	public readonly heroSliderConfig: SwiperConfigInterface = {
		observer: true,
		autoHeight: true,
		slidesPerView: 1,
		navigation: {
			nextEl: '.slider__hero .swiper-button-next',
			prevEl: '.slider__hero .swiper-button-prev',
		},
		pagination: {
			el: '.slider__hero .swiper-pagination',
			type: 'bullets',
			clickable: true,
		},
		autoplay: true
	};

	public slides$: Observable<Entity[]>;

	constructor(
		private _cms: CMSService,
	) {}

	ngOnInit() {
		this.slides$ = this._cms.getPosts('slides', 1, {
			loadPostsImmediately: true,
			filter: {
				type: 'slider',
			}
		});
	}
}
