import { Component, OnInit } from '@angular/core';
import { CMSService, PostState } from '@app/cms';
import { Observable, BehaviorSubject, combineLatest } from 'rxjs';
import { FormControl } from '@angular/forms';
import { withLatestFrom, map, tap, mapTo, startWith } from 'rxjs/operators';

type Recipe = PostState;
type Recipes = Recipe[];

@Component({
	selector: 'page-recipes',
	templateUrl: 'recipes.component.html',
	styleUrls: ['recipes.component.scss'],
})
export class RecipesComponent implements OnInit {
	public recipes$: Observable<Recipes>;
	public category = new FormControl('');
	public filter$: Observable<string>;

	constructor(
		private _cms: CMSService,
	) {}

	ngOnInit() {
		this.filter$ = this.category.valueChanges.pipe(
			startWith('')
		);

		this.recipes$ = combineLatest(
			this._cms.getPosts('recipes', 1, {
				loadPostsImmediately: true,
				filter: {
					type: 'recipe',
					count: 200
				}
			}),
			this.filter$,
		)
		.pipe(
			map(([recipes, filter]) => filter ? recipes.filter(recipe => recipe.category === filter) : recipes)
		);
	}
}
