import { Component } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { FileUploadValidators } from '@iplab/ngx-file-upload';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { environment } from '@app-env/environment';
import { tap, pluck } from 'rxjs/operators';
import { ErrorService } from '@app/core/services/error';
import { MessageModalComponent } from '@app/shared/modals/message/message.component';

@Component({
	selector: 'page-support',
	templateUrl: 'support.component.html',
	styleUrls: ['support.component.scss'],
})
export class SupportComponent {
	public questionForm = new FormGroup({
		name: new FormControl(null, [
			Validators.required
		]),
		email: new FormControl(null, [
			Validators.email,
			Validators.required
		]),
		phone_subscriber: new FormControl(null, [
			Validators.required
		]),
		company_name: new FormControl(null, [
			Validators.required
		]),
		purpose: new FormControl(null, [
			Validators.required
		]),
		event_location: new FormControl(null, [
			Validators.required
		]),
		event_date: new FormControl(null, [
			Validators.required
		]),
		guest_number: new FormControl(null, [
			Validators.required
		]),
		files: new FormControl(null,
			FileUploadValidators.filesLimit(5)
		),
		accept_terms: new FormControl(false, [
			Validators.requiredTrue
		])
	});

	public errorMessages: string[] = [];

	public submitDisabled = false;

	constructor(
		private _http: HttpClient,
		private _modalService: NgbModal,
		public errorProvider: ErrorService,
	) {}

	public submitQuestionForm() {
		this.submitDisabled = true;
		this.errorMessages = [];

		const endpoint = environment.API_URL + 'send-question-mail';
		const formData: FormData = new FormData();

		for (const [field, value] of Object.entries(this.questionForm.value)) {
			if (value === null || value === undefined) continue;
			if (field === 'files' && Array.isArray(value)) {
				for (let i = 0; i < value.length; i++) {
					formData.append(`files[${i}]`, value[i], value[i].name);
				}
			} else if (typeof value === 'string' || typeof value === 'number') {
				formData.append(field, value.toString());
			} else {
				formData.append(field, JSON.stringify(value));
			}
		}

		console.log(`XHR to ${endpoint} sent.`, formData.toString());
		this._http.post(endpoint, formData).pipe(
			tap(response => console.log(`XHR to ${endpoint} finished.`)),
			pluck('data'),
		).subscribe(
			(result) => {
				console.log('send-question-mail request succeeded.', result);
				this.showMessageModal({
					title: 'Kapcsolat',
					message: '<p>Köszönjük, hamarosan válaszolunk a levélre!</p>',
				});
				this.submitDisabled = false;
				this.questionForm.reset();
				this.questionForm.controls.files.setValue([]);
			},
			(response: HttpErrorResponse) => {
				console.warn('send-question-mail request rejected.', response);
				const errors = response.error.errors as any[];
				this.errorMessages = errors.map(error => this.errorProvider.get(error.code || error));
				this.submitDisabled = false;
			},
		);
	}

	public showMessageModal({
		title,
		message,
	}: {
		title: string,
		message: string,
	}): Promise<any> {
		const modal = this._modalService.open(MessageModalComponent);
		modal.componentInstance.title = title;
		modal.componentInstance.message = message;
		return modal.result;
	}
}
