import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent, CategoriesComponent, FruitWorldComponent, ProductsComponent, NatureAndUsComponent, RecipesComponent, RecipeComponent, ThinkAgainComponent, ThanksComponent, SupportComponent, SmallFruitAdverbComponent, HowMadeComponent, SchoolBagProgramComponent, DrawingCompetitionComponent, XmasComponent, JuiceComponent, BeesComponent, SupportAndDonationComponent } from './pages';


const routes: Routes = [
	{
		path: '',
		component: HomeComponent,
	},
	{
		path: 'termekek',
		component: CategoriesComponent,
	},
	{
		path: 'termekek/:category',
		redirectTo: 'termekek/:category/',
	},
	{
		path: 'termekek/:category/:product',
		component: ProductsComponent,
	},
	{
		path: 'juice',
		component: JuiceComponent,
	},
	{
		path: 'juice/:product',
		component: JuiceComponent,
	},
	{
		path: 'gyumolcsvilag',
		component: FruitWorldComponent,
	},
	{
		path: 'gyumolcsvilag/hogyan-keszult',
		component: HowMadeComponent,
	},
	{
		path: 'gyumolcsvilag/gyumolcsgastronomia',
		component: RecipesComponent,
	},
	{
		path: 'gyumolcsvilag/gyumolcsgastronomia/:recipe',
		component: RecipeComponent,
	},
	{
		path: 'gyumolcsvilag/kis-gyumolcshatarozo',
		component: SmallFruitAdverbComponent,
	},
	{
		path: 'a-termeszet-es-mi',
		component: NatureAndUsComponent,
	},
	{
		path: 'gondold-ujra',
		component: ThinkAgainComponent,
	},
	{
		path: 'koszonjuk-sio',
		component: ThanksComponent,
	},
	{
		path: 'koszonjuk-sio/iskolataska-program',
		component: SchoolBagProgramComponent,
	},
	{
		path: 'koszonjuk-sio/rajzverseny',
		component: DrawingCompetitionComponent,
	},
	{
		path: 'termektamogatas',
		component: SupportComponent,
	},
	{
		path: 'gondosgazdak',
		redirectTo: '/gyumolcsvilag',
	},
	{
		path: 'mindenunk-a-gyumolcs',
		redirectTo: '/gyumolcsvilag',
	},
	{
		path: 'mehek-es-a-sio',
		component: BeesComponent,
	},
	{
		path: 'tamogatas',
		component: SupportAndDonationComponent,
	},
	{
		path: 'karacsony',
		component: XmasComponent,
	},
];

RouterModule.forRoot(routes, {
	// onSameUrlNavigation: 'reload',
	anchorScrolling: 'enabled',
	enableTracing: false
});

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule]
})
export class HomeRoutingModule {}
