import { Component } from '@angular/core';
import { SwiperConfigInterface, SwiperNavigationInterface, SwiperPaginationInterface } from 'ngx-swiper-wrapper';

@Component({
	selector: 'section[diy]',
	templateUrl: 'diy.component.html',
	styleUrls: ['diy.component.scss'],
})
export class DiyComponent {
	private navigation: SwiperNavigationInterface = {
		nextEl: '.slider__diy .swiper-button-next',
		prevEl: '.slider__diy .swiper-button-prev',
	};

	private pagination: SwiperPaginationInterface = {
		el: '.slider__diy .swiper-pagination',
		type: 'bullets',
		clickable: true,
	};

	public threeColSliderConfig: SwiperConfigInterface = {
		slidesPerView: 3,
		navigation: this.navigation,
		pagination: this.pagination,
		breakpoints: {
			576: {
				slidesPerView: 1
			},
			768: {
				slidesPerView: 2
			}
		}
	};
}
