import { Component, OnInit } from '@angular/core';
import { Category, PostState, CMSService } from '@app/cms';
import { SwiperConfigInterface } from 'ngx-swiper-wrapper';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

type Product = PostState;
type Products = Product[];

@Component({
	selector: 'page-xmas',
	templateUrl: 'xmas.component.html',
	styleUrls: ['xmas.component.scss'],
})
export class XmasComponent implements OnInit {
	public readonly threeColSliderConfig: SwiperConfigInterface = {
		observer: true,
		slidesPerView: 3,
		navigation: {
			nextEl: '.slider__products .swiper-button-next',
			prevEl: '.slider__products .swiper-button-prev',
		},
		pagination: {
			el: '.slider__products .swiper-pagination',
			type: 'bullets',
		},
		breakpoints: {
			576: {
				slidesPerView: 1
			},
			768: {
				slidesPerView: 2
			}
		}
	};

	public products$: Observable<Products>;
	public filtered: string[] = ['natura'];

	constructor(
		private _cms: CMSService,
	) {}

	ngOnInit(): void {
		this.products$ = this._cms.getPosts('products', 1, {
			loadPostsImmediately: true,
			filter: {
				type: 'product',
				count: 200
			}
		}).pipe(
			map(
				products => products.filter(
					product => this.filtered[0] === product.categories[0]
				)
			)
		);
	}
}
