import { Component, Input, OnInit, ViewChild } from '@angular/core';
import { SwiperConfigInterface, SwiperDirective } from 'ngx-swiper-wrapper';

@Component({
	selector: 'section[tab-slider]',
	templateUrl: 'tab-slider.component.html',
	styleUrls: ['tab-slider.component.scss'],
})
export class TabSliderComponent implements OnInit {
	@ViewChild(SwiperDirective, { static: false }) directiveRef?: SwiperDirective;

	@Input() public dataIndex: number;
	@Input() public data: any;
	public index = 0;

	public readonly tabSliderConfig: SwiperConfigInterface = {
		observer: true,
		// autoHeight: true,
		slidesPerView: 1,
		navigation: false,
		pagination: {
			el: '.slider__tab .swiper-pagination',
			type: 'bullets',
			clickable: true,
		},
	};

	ngOnInit() {
		console.log(this.data);
		console.log(this.dataIndex);
	}

	goToSlide(index) {
		console.log(index);
		this.directiveRef.setIndex(index);
	}
}
