import { Component, OnInit, ChangeDetectionStrategy } from '@angular/core';
import { CMSService, Category, PostState, Tags, Tag } from '@app/cms';
import { tap, map, withLatestFrom, filter } from 'rxjs/operators';
import { Observable, combineLatest, of, BehaviorSubject } from 'rxjs';

type Product = PostState;
type CategoryWithPackshots = Category & {
	packshots: {
		src: string;
		alt: string;
		slug: string;
	}[];
	productCount: number;
};

@Component({
	selector: 'page-categories',
	templateUrl: 'categories.component.html',
	styleUrls: ['categories.component.scss'],
	changeDetection: ChangeDetectionStrategy.OnPush
})
export class CategoriesComponent implements OnInit {
	public tags$: Observable<Tags>;
	public filter$ = new BehaviorSubject(null);
	public filterTag$: Observable<Tag>;

	public categoriesWithPackshots$: Observable<CategoryWithPackshots[]>;

	public readonly size = {
		'1 l': '1l',
		'3 l': '3l',
		'2 dl': '2dl',
		'2.5 dl': '2.5dl',
		'3.3 dl': '3.3dl',
		'5 dl': '5dl',
		'7 dl': '7dl',
		'90 g': '90g',
		'120 g': '120g',
	};

	constructor(
		private _cms: CMSService
	) {}

	ngOnInit() {
		this.tags$ = this._cms.getTags();

		const categories$ = this._cms.getCategories();
		const products$ = this._cms.getPosts('products', 1, {
			loadPostsImmediately: true,
			filter: {
				type: 'product',
				count: 200
			}
		});

		const generatePackshotUrl = (product: Product, category: Category) => {
			if (typeof product.packshot.product_small !== 'undefined') {
				return product.packshot.product_small.image_url;
			} else {
				console.warn('missing packshot', `${category.name} - ${product.title} ${product.package}`);
				return `https://static.hpsdigital.hu/siogyumolcs.hu/assets/img/categories/products/${category.slug}-${this.size[product.package]}-${product.slug}.png`;
			}
		};
		const generatePackshotData = (product: Product, category: Category) => ({
			src: generatePackshotUrl(product, category),
			alt: `${category.name} - ${product.title} ${product.package}`,
			slug: `${product.slug}-${this.size[product.package]}`,
		});

		this.categoriesWithPackshots$ = combineLatest(
			categories$,
			products$,
			this.filter$,
		).pipe(
			// wait until everyting is loaded
			filter(([categories, products]) => categories.length > 0 && products.length > 0),
			// add packshot data to category
			map(([categories, products, tag]) => categories
				.map(category => {
					const categoryProducts = products.filter(product => product.categories.includes(category.slug));
					const filteredCategoryProducts = tag ? categoryProducts.filter(product => product.tags.includes(tag)) : categoryProducts;
					const featuredCategoryProducts = categoryProducts.filter(product => product.featured);
					const packshots = (tag ? filteredCategoryProducts : featuredCategoryProducts)
						.slice(0, 4)
						.map(product => generatePackshotData(product, category))
					;

					let slug = category.slug;
					let mainRoute = '/termekek';
					if (category.slug == 'juice') {
						mainRoute = '/juice';
						slug = '';
					}

					return {
						...category,
						mainRoute,
						slug,
						packshots,
						productCount: filteredCategoryProducts.length,
					};
				})
				.filter(category => category.productCount > 0)
			),
			tap(x => console.log('categories with packshots', x)),
		);

		this.filterTag$ = this.filter$.pipe(
			withLatestFrom(this.tags$),
			map(([filterSlug, tags]) => tags.find(tag => tag.slug === filterSlug)),
		);
	}
}
