import { Component, ViewChild, OnInit, ChangeDetectionStrategy } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { SwiperDirective, SwiperConfigInterface } from 'ngx-swiper-wrapper';
import { CMSService, PostState, Category } from '@app/cms';

import { map, tap, withLatestFrom, shareReplay, distinctUntilChanged } from 'rxjs/operators';
import { combineLatest, Observable, Subject } from 'rxjs';
import { Location } from '@angular/common';

import dayjs from 'dayjs/esm';
import isSameOrBefore from 'dayjs/esm/plugin/isSameOrBefore';
dayjs.extend(isSameOrBefore);


type Product = PostState;
type CategoryWithProducts = Category & {
	products: Product[];
};

@Component({
	selector: 'page-products',
	templateUrl: 'products.component.html',
	styleUrls: ['products.component.scss'],
	changeDetection: ChangeDetectionStrategy.OnPush
})
export class JuiceComponent implements OnInit {

	public readonly size = {
		'1 l': '1l',
		'3 l': '3l',
		'2 dl': '2dl',
		'2.5 dl': '2.5dl',
		'3.3 dl': '3.3dl',
		'5 dl': '5dl',
		'7 dl': '7dl',
		'7.5 dl': '7.5dl',
		'90 g': '90g',
		'120 g': '120g',
	};

	public readonly relativePackshotSizes = {
		'1l' : 1,
		'3l' : .5,
		'2dl' : .6,
		'2.5dl' : .65,
		'3.3dl' : .75,
		'5dl' : .85,
		'7dl' : .9,
		'7.5dl' : .95,
		'90g' : .65,
		'120g': .65,
	};

	public readonly sliderHeights = {
		lg: window.innerWidth < 768 ? 380 : 570,
	};

	public readonly productsPackshotSliderConfig: SwiperConfigInterface = {
		// observer: true,
		initialSlide: 0,
		slidesPerView: 4,
		centeredSlides: false,
		loopAdditionalSlides: 100,
		// spaceBetween: 70,
		navigation: false,
		loop: true,
		pagination: {
			el: '.slider__products .swiper-pagination',
			type: 'bullets',
			clickable: true,
		},
		breakpoints: {
			1600: {
				slidesPerView: 3
			},
			1300: {
				slidesPerView: 2
			},
			900: {
				slidesPerView: 2
			}
		}
	};

	public readonly productsDataSliderConfig: SwiperConfigInterface = {
		// observer: true,
		initialSlide: 0,
		allowTouchMove: false,
		loop: true,
		loopAdditionalSlides: 100,

		navigation: {
			nextEl: '.slider__products-data .swiper-button-next',
			prevEl: '.slider__products-data .swiper-button-prev',
		},
		pagination: false,
	};

	public readonly productsShopSliderConfig: SwiperConfigInterface = {
		// observer: true,
		initialSlide: 0,
		allowTouchMove: false,
		loop: true,
		loopAdditionalSlides: 100,
		pagination: false,
	};

	@ViewChild('productsPackshotSlider', { static: false }) productsPackshotSlider?: SwiperDirective;
	@ViewChild('productsDataSlider', { static: false }) productsDataSlider?: SwiperDirective;
	@ViewChild('productsShopSlider', { static: false }) productsShopSlider?: SwiperDirective;

	public selectedCategory$: Observable<Category>;
	public selectedCategoryWithProducts$: Observable<CategoryWithProducts>;

	public swiperIndexChange = new Subject<number>();

	public isInfectedEnded = dayjs().isAfter(dayjs('2021-05-24'));

	public swipeIndex = 0

	constructor(
		private _cms: CMSService,
		private _router: Router,
		private _activatedRoute: ActivatedRoute,
		private _location: Location,
	) {}

	ngOnInit() {
		const categories$ = this._cms.getCategories();
		const products$ = this._cms.getPosts('products', 1, {
			loadPostsImmediately: true,
			filter: {
				type: 'product',
				count: 200
			}
		});
		const categoriesWithProducts$ = combineLatest(
			categories$,
			products$,
		).pipe(
			// add products to categories
			map(([categories, products]) => categories
				.map(category => ({
					...category,
					products: products.filter(product => product.categories.includes(category.slug))
				}))
			),
		);

		const categorySlug$ = this._activatedRoute.paramMap.pipe(
			map(params => 'juice'),
			distinctUntilChanged(),
		);

		const generatePackshotData = (product: Product, category: Category) => {
			const sliderHeight = this.sliderHeights.lg;
			const packshotData: {
				src: string,
				width: number,
				height: number,
				marginTop: number,
				alt: string,
			} = {
				src: null,
				width: null,
				height: sliderHeight,
				marginTop: 0,
				alt: `${category.name} - ${product.title} ${product.package}`,
			};
			if (typeof product.packshot.product_large !== 'undefined') {
				packshotData.src = product.packshot.product_large.image_url;
				packshotData.height = Math.round(packshotData.height * this.relativePackshotSizes[this.size[product.package]]);
				const resizeRatio = packshotData.height / product.packshot.product_large.height;
				packshotData.width = Math.round(product.packshot.product_large.width * resizeRatio);
				packshotData.marginTop = sliderHeight - packshotData.height;
			} else {
				console.warn('missing packshot', `${category.name} - ${product.title} ${product.package}`);
				packshotData.src = `https://siogyumolcs.hu/assets/img/categories/products/${category.slug}-${this.size[product.package]}-${product.slug}.png`;
			}
			return packshotData;
		};

		this.selectedCategoryWithProducts$ = combineLatest(
			categoriesWithProducts$,
			categorySlug$,
		).pipe(
			// find selected (by route param) category
			map(([categories, categorySlug]) => ({
				...categories.find(category => 'juice' === categorySlug),
			})),
			map(category => ({
				...category,
				products: category.products.map(product => ({
					...product,
					packshot: generatePackshotData(product, category),
				})),
			})),
			tap(x => console.log('selected category with products', x)),
			shareReplay(1),
		);

		this.swiperIndexChange.pipe(
			withLatestFrom(this.selectedCategoryWithProducts$),
			map(([index, category]) => ({
				index,
				category_slug: category.slug,
				package_slug: category.products[index].package_slug,
			})),
		).subscribe(locationData => {
			console.log('updating location on swiper index change', locationData.index);
			this._location.replaceState(`/${locationData.category_slug}/${locationData.package_slug}`);
		});
	}

	private _initializedSwipers: { [key: string]: any } = {};
	onSwiperInit(swiperName: string, swiperInstance: any) {
		console.log('onSwiperInit', swiperName, swiperInstance);
		this._initializedSwipers[swiperName] = swiperInstance;

		// https://github.com/nolimits4web/swiper/issues/2399
		this._initializedSwipers[swiperName].on('update', () => {
			console.log('swiper internal update event', swiperName);
			if (this._initializedSwipers[swiperName].activeIndex !== 0) {
				console.log('using swiper workaround...');
				this._initializedSwipers[swiperName].slideTo(0, 0);
			}
		});

		if (Object.keys(this._initializedSwipers).length >= 2) {
			const productsPackshotSlider = this._initializedSwipers['productsPackshotSlider'];
			const productsDataSlider = this._initializedSwipers['productsDataSlider'];
			//const productsShopSlider = this._initializedSwipers['productsShopSlider'];

			// productsPackshotSlider.controller.control = productsDataSlider;
			// productsDataSlider.controller.control = productsPackshotSlider;
		}
	}

	onPackshotClick(index: number) {
		this.productsPackshotSlider.setIndex(index);
		this.productsDataSlider.setIndex(index);
		this.swipeIndex = index
	}

	onIndexChange(index: number) {
		console.log('onIndexChange', index);
		this.swiperIndexChange.next(index);
		this.productsPackshotSlider.setIndex(index);
		this.swipeIndex = index
	}

	onPackshotIndexChange(index: number) {
		this.productsDataSlider.setIndex(index);
		this.swipeIndex = index
	}

	keepOrder = (a: any, b: any) => 0;
}
