import { Component, OnInit } from '@angular/core';
import { PostState, CMSService } from '@app/cms';
import { ActivatedRoute } from '@angular/router';

import { map, tap, withLatestFrom, shareReplay, distinctUntilChanged } from 'rxjs/operators';
import { combineLatest, Observable, Subject } from 'rxjs';

type Recipe = PostState;

@Component({
	selector: 'page-recipe',
	templateUrl: 'recipe.component.html',
	styleUrls: ['recipe.component.scss'],
})
export class RecipeComponent implements OnInit {
	public selectedRecipe$: Observable<Recipe>;

	constructor(
		private _cms: CMSService,
		private _activatedRoute: ActivatedRoute,
	) {}

	ngOnInit() {
		const recipeSlug$ = this._activatedRoute.paramMap.pipe(
			map(params => params.get('recipe')),
			distinctUntilChanged(),
		);

		const recipes$ = this._cms.getPosts('recipes', 1, {
			loadPostsImmediately: true,
			filter: {
				type: 'recipe',
				count: 200
			}
		});

		this.selectedRecipe$ = combineLatest(
			recipes$,
			recipeSlug$
		).pipe(
			map(([recipes, recipeSlug]) => ({
				...recipes.find(recipe => recipe.slug === recipeSlug),
			})),
			tap(x => console.log('selected recipe with slug', x)),
			shareReplay(1),
		);
	}
}
