import { Component, OnInit, ChangeDetectionStrategy } from '@angular/core';
import { SwiperConfigInterface } from 'ngx-swiper-wrapper';
import { CMSService, Entity } from '@app/cms';
import { Observable } from 'rxjs';

@Component({
	selector: 'page-bees',
	templateUrl: 'bees.component.html',
	styleUrls: ['bees.component.scss'],
	changeDetection: ChangeDetectionStrategy.OnPush,
})
export class BeesComponent implements OnInit {

	public readonly farmerSliderConfig: SwiperConfigInterface = {
		observer: true,
		slidesPerView: 1,
		navigation: {
			nextEl: '.slider__farmer .swiper-button-next',
			prevEl: '.slider__farmer .swiper-button-prev',
		},
		pagination: {
			el: '.slider__farmer .swiper-pagination',
			type: 'bullets',
			clickable: true,
		},
	};

	public readonly threeColSliderConfig: SwiperConfigInterface = {
		observer: true,
		initialSlide: 0,
		slidesPerView: 3,
		navigation: {
			nextEl: '.slider__gastro .swiper-button-next',
			prevEl: '.slider__gastro .swiper-button-prev',
		},
		pagination: {
			el: '.slider__gastro .swiper-pagination',
			type: 'bullets',
			clickable: true,
		},
		breakpoints: {
			576: {
				slidesPerView: 1
			},
			768: {
				slidesPerView: 2
			}
		}
	};

	public farmers$: Observable<Entity[]>;
	public recipes$: Observable<Entity[]>;

	constructor(
		private _cms: CMSService,
	) {}

	ngOnInit() {
		this.farmers$ = this._cms.getPosts('farmers', 1, {
			loadPostsImmediately: true,
			filter: {
				type: 'farmer',
				count: 200
			}
		});

		this.recipes$ = this._cms.getPosts('recipes', 1, {
			loadPostsImmediately: true,
			filter: {
				type: 'recipe',
				count: 6
			}
		});
	}
}
